.top-slider {
    padding: 65px 0 45px;
    background-image: url("../images/bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-color: $bg;

    &__container {
        max-width: 1560px;
    }

    &__content {
        padding-top: 125px;
        padding-left: 175px;
    }

    &__item {
        display: flex !important;
        justify-content: space-between;
    }

    &__title {
        margin-bottom: 35px;
        max-width: 670px;
        font-size: 58px;
        line-height: 68px;
        color: $title;

        @extend %roboto-700;
    }

    &__text {
        margin-bottom: 55px;
        max-width: 470px;
    }

    &__link {
        padding: 14px 36px;

        @extend %btn;
    }

    .slick-dots {
        display: flex;
        justify-content: center;
        margin-top: -70px;

        li {
            padding: 0 5px;
            line-height: 0;

            button {
                position: relative;
                padding: 0;
                width: 26px;
                height: 26px;
                border: 1px solid transparent;
                border-radius: 50%;
                font-size: 0;
                background-color: transparent;

                &::before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background-color: $default;
                    transform: translate(-50%, -50%);
                }
            }

            &.slick-active {
                button {
                    border-color: $accent;

                    &::before {
                        background-color: $accent;
                    }
                }
            }
        }
    }
}
